/**
 * Intializes slider on book page for the reviews and the cta.
 */
function announcementSlider() {
  /**
   * Update the pagination buttons on change.
   */
  function onChange() {
    const sliderID = this.selector.getAttribute('id');

    // Remove active class from all slide controls.
    const slideControls = document.querySelector(`.${sliderID}-controls`);
    const controls = slideControls.querySelectorAll('.slide-control');
    Array.prototype.map.call(controls, (control) => {
      control.classList.remove('active');
    });

    // Add active class to current slide control.
    const activeSlideControl = slideControls.querySelector(
      `.siema-control-${this.currentSlide}`
    );
    activeSlideControl.classList.add('active');
  }

  /**
   * Adds pagination buttons DOM.
   */
  function addPagination() {
    const sliderID = this.selector.getAttribute('id');

    // create button container
    // @todo make these list items for better a11y.
    const buttonContainer = document.createElement('div');
    buttonContainer.classList.add('cta__buttons');
    buttonContainer.classList.add(`${sliderID}-controls`);
    this.selector.insertAdjacentElement('afterend', buttonContainer);

    if (1 === this.innerElements.length) {
      return;
    }

    this.innerElements.forEach((el, idx) => {
      // Create button to navigate slides.
      const btn = document.createElement('button');
      btn.classList.add('slide-control');
      btn.classList.add(`siema-control-${idx}`);
      btn.addEventListener('click', () => this.goTo(idx));
      buttonContainer.appendChild(btn);

      // Make first one active.
      if (0 === idx) {
        btn.classList.add('active');
      }

      // Create label for button
      const label = document.createElement('span');
      label.textContent = `Go to slide ${idx}`;
      label.classList.add('screen-reader-text');
      btn.appendChild(label);
    });

    setInterval(() => this.next(), 3000);
  }

  Siema.prototype.addPagination = addPagination;

  /**
   * Initalize slideshows.
   */
  Array.from(document.querySelectorAll('.siema'))
    .map((selector) => new Siema({
      selector,
      onChange,
      loop: true,
    }))
    .map((siema) => {
      siema.addPagination();
      return siema;
    });
}

function toggleHeaderSearch() {
  const searchToggle = document.getElementById('site-header__search-toggle');
  const searchWrapper = document.getElementById('site-header__search-wrapper');

  if ((searchToggle) && (searchWrapper)) {
    searchToggle.addEventListener('click', (e) => {
      searchWrapper.classList.toggle('toggle__mobile-search--active');
      searchToggle.setAttribute(
        'aria-expanded',
        `${'true' !== searchToggle.getAttribute('aria-expanded')}`
      );
      e.preventDefault();
    });
  }
}

function toggleMobileNav() {
  const menuToggle = document.getElementById('site-header__menu-toggle');
  const menuClose = document.getElementById('site-header__mobile-close');
  const pageWrapper = document.body;

  if ((menuToggle) && (pageWrapper)) {
    menuToggle.addEventListener('click', (e) => {
      pageWrapper.classList.add('toggle__mobile-nav--active');
      e.preventDefault();
    });
  }

  if ((menuToggle) && (pageWrapper)) {
    menuClose.addEventListener('click', (e) => {
      pageWrapper.classList.remove('toggle__mobile-nav--active');
      e.preventDefault();
    });
  }
}

function closeHeaderSearch() {
  const searchToggle = document.getElementById('site-header__search-toggle');
  const searchWrapper = document.getElementById('site-header__search-wrapper');
  searchWrapper.classList.remove('toggle__mobile-search--active');
  searchToggle.setAttribute(
    'aria-expanded',
    `${'false' !== searchToggle.getAttribute('aria-expanded')}`
  );
}

function prepareLoadMore() {
  const button = document.getElementById('trigger-load-more');
  if (null != button && '' === button.value) {
    button.addEventListener('click', doLoadMore);
  }
}

/**
 * When a load more button is pressed, query for the next series of posts and
 * append them to the div above the load more button.
 * @param  {object} e Event
 * @return {void}
 */
function doLoadMore(e) {
  e.preventDefault();

  const elem = e.currentTarget.parentElement;
  const button = e.currentTarget;

  elem.classList.toggle('loading');

  const { ajaxPath, page } = button.dataset;

  if ('string' !== typeof ajaxPath || 0 === ajaxPath.length) {
    return;
  }

  fetch(ajaxPath)
    .then((resp) => resp.text())
    .then((body) => {
      const tempEl = createNewElementAndAppendFirstChild(elem, body);

      /**
       * Build the elements necessary to update the window history.
       */
      const newPage = page ?
        Number(page) + 1 :
        Number(window.location.hash.substr(1)) + 1 || 1;
      const title = `All Posts – Page ${newPage} – Andreessen Horowitz`;
      const builtURL =
        `${window.location.origin}${window.location.pathname}#${newPage}`;
      const { pages: prevPages } = window.history.state || { pages: [] };
      const pages = prevPages.concat({ body, newPage });

      // Update the window history
      window.history.pushState(
        { pages },
        title,
        builtURL
      );

      updateLoadMoreButton(tempEl, elem, newPage);
    })
    .catch(() => {
      const previousSibling = button.previousElementSibling;
      let errorEl = (
        previousSibling &&
          true === previousSibling.classList.contains('load-more__error')
      ) ?
        previousSibling :
        null;

      // create and insert new error if it doesn't exist
      if (null === errorEl) {
        errorEl = document.createElement('p');
        errorEl.classList.add('load-more__error');
        button.insertAdjacentElement('beforebegin', errorEl);
      }
      // eslint-disable-next-line max-len
      errorEl.textContent = 'Oops! Something went wrong while loading more posts.';
    });
}

/**
 * If there are pages in the window history, add them to the list of stories.
 * @return {void}
 */
function loadPreviousHistory() {
  if (
    ! window.history.state ||
    ! window.history.state.pages ||
    ! document.querySelector('.load-more')
  ) {
    return;
  }

  const { pages } = window.history.state;

  // Add the stories back to the page
  pages.forEach(({ newPage, body }) => {
    const elem = document.querySelector('.load-more');
    if (! elem) {
      return;
    }

    const tempEl = createNewElementAndAppendFirstChild(elem, body);
    updateLoadMoreButton(tempEl, elem, newPage);
  });
}

/**
 * Use the load more button in the new body and replace the current load more
 * button.
 * @param  {object} newBody       HTMLElement containing the new button.
 * @param  {object} elemToReplace HTMLElement with previous load more button.
 * @param  {number} newPageNumber New page number
 * @return {void}
 */
function updateLoadMoreButton(newBody, elemToReplace, newPageNumber) {
  // Typecheck parameters
  if (
    ! (newBody instanceof HTMLElement) ||
    ! (elemToReplace instanceof HTMLElement) ||
    ! Number.isInteger(newPageNumber)
  ) {
    return;
  }

  // Replace loadmore button
  const newLoadMore = newBody.querySelector('.load-more');
  if (newLoadMore) {
    elemToReplace.parentNode.replaceChild(newLoadMore, elemToReplace);

    // Update new element page
    const newButton = newLoadMore.querySelector('button');
    newButton.dataset.page = newPageNumber || 1;

    // Apply listener to this element.
    prepareLoadMore();
  } else {
    elemToReplace.parentNode.removeChild(elemToReplace);
  }
}

/**
 * Given an element to append to and the html to append, append the fisrt child
 * of that html element to the element to append to.
 * @param  {object} elementToAppendTo         HTMLElement to append to.
 * @param  {string} htmlToCreateElementFrom   String of HTML from which to
 *                                            create element.
 * @return {object}                           HTMLElement of the new html.
 */
function createNewElementAndAppendFirstChild(
  elementToAppendTo,
  htmlToCreateElementFrom
) {
  const tempEl = document.createElement('div');
  tempEl.innerHTML = htmlToCreateElementFrom;
  elementToAppendTo.insertAdjacentElement('beforebegin', tempEl.firstChild);
  return tempEl;
}

// eslint-disable-next-line no-unused-vars
const isInViewport = (el) => {
  const bounding = el.getBoundingClientRect();
  return (
    0 <= bounding.top &&
    bounding.bottom <=
    (window.innerHeight || document.documentElement.clientHeight)
  );
};

function throttle(func, delay = 500) {
  let timeout = null;
  return function (...args) {
    if (! timeout) {
      timeout = setTimeout(() => {
        func.call(this, ...args);
        timeout = null;
      }, delay);
    }
  };
}

function tab() {
  $('.tab-content > div').hide();
  const activetab = $('.tab-nav .active a').attr('href');
  $(activetab).show();
  $('.tab-nav a').click(function () {
    $('.tab-nav li').removeClass('active');
    $(this).parent().addClass('active');
    const currentTab = $(this).attr('href');
    $('.tab-content > div').hide();
    $(currentTab).show();
    return false;
  });
}

