function eventOnScroll(elems) {
  elems.each((idx, item) => {
    const el = $(item);
    // const parent = el.parents('.tab-content--item');
    const parent = el.parents('.section-news');
    if (parent.hasClass('is-appeared')) return;
    if (isInViewport(el[0])) {
      parent.addClass('is-appeared');
    }
  });
}

function homeAnimations() {
  // const buttons = $('.tab-content--item .load-more__button')
  const buttons = $('.section-news .load-more__button')
    .not('.is-appeared');
  if (buttons.length) {
    window.addEventListener('scroll',
      throttle(() => {
        eventOnScroll(buttons);
      }, 200));
  }
}

function homePopularPagination() {
  const wrapper = $('.tab-content--item#popular');
  if (wrapper.length) {
    const button = wrapper.find('#load-more-popular');
    const posts = wrapper.find('.post-popular.post-popular--hidden');
    if (7 > posts.length) {
      button.parent().remove();
    }
    posts.slice(0, 6).removeClass('post-popular--hidden');
    button.on('click', (e) => {
      e.preventDefault();
      const loadPosts = wrapper.find('.post-popular.post-popular--hidden');
      if (7 > loadPosts.length) {
        button.parent().remove();
      }
      loadPosts.slice(0, 6).removeClass('post-popular--hidden');
    });
  }
}

function inlineSvg() {
  $('.inline-this-svg').each(function () {
    const el = $(this);
    const img = el.find('> img');
    if (0 < img.length) {
      $.get(img[0].src, (svgDoc) => {
        const svgEl =
          document.importNode(svgDoc.documentElement, true);
        el.html(svgEl);
        // const viewBoxHeight = ('' !== svgEl.viewBox.baseVal.height)
        //   ? svgEl.viewBox.baseVal.height : '';
        // el.find('svg').attr('height', viewBoxHeight);
      });
    }
  });
}

