function txoIntroToggle() {
  Array.prototype.slice.call(document.querySelectorAll('.txo-intro__button'))
    .forEach((element) => {
      element.addEventListener('click', (e) => {
        const button = element;
        e.preventDefault();
        const textWrap = $(`#${button.getAttribute('aria-controls')}`)[0];
        const isExpanded = 'true' === textWrap.getAttribute('aria-expanded');
        if (! isExpanded) {
          button.innerText = '« Read less';
          textWrap.setAttribute('aria-expanded', 'true');
        } else {
          button.innerText = 'Read more »';
          textWrap.setAttribute('aria-expanded', 'false');
        }
      });
    });
}

function txoFilterCommunity() {
  Array.prototype.slice.call(document.querySelectorAll('[data-team-filter]'))
    .forEach((element) => {
      element.addEventListener('click', (e) => {
        e.preventDefault();
        const button = element;
        const buttons = document.querySelectorAll('[data-team-filter]');
        const filterTarget = button.getAttribute('data-team-filter');
        const companies = document.querySelectorAll('[data-team-filter-val]');
        Array.prototype.forEach.call(companies, (company) => {
          const el = company;
          const { teamFilterVal } = el.dataset;

          // Show all companies.
          el.setAttribute('aria-hidden', false);

          // Do no filter if showing all.
          if ('all' === filterTarget) {
            return;
          }

          // Subcategory has multiple values
          if (teamFilterVal.indexOf(';')) {
            const subcategories = teamFilterVal.split(';');
            let hidden = true;
            Array.prototype.forEach.call(subcategories, (item) => {
              if (filterTarget === item.trim()) {
                hidden = false;
              }
            });
            el.setAttribute('aria-hidden', hidden);
          } else if (filterTarget !== teamFilterVal) {
            // Otherwise hide if the filter does not match.
            el.setAttribute('aria-hidden', true);
          }
        });
        Array.prototype.forEach.call(buttons, (btn) => {
          const { teamFilter: buttonFilterTarget } = btn.dataset;
          const el = btn;
          if (filterTarget === buttonFilterTarget) {
            el.setAttribute('aria-selected', true);
          } else {
            el.setAttribute('aria-selected', false);
          }
        });
      });
    });
}

function txoStickyNavigation() {
  const stickyNav = document.getElementById('sticky-navigation');
  if (stickyNav) {
    const hero = document.getElementById('hero');
    window.addEventListener('scroll', () => {
      const offset = (hero) ? hero.offsetHeight : 0;
      const currentScroll = window.scrollY;
      if (currentScroll > offset) {
        if (! stickyNav.classList.contains('is-sticky')) {
          stickyNav.classList.add('is-sticky');
        }
      } else if (stickyNav.classList.contains('is-sticky')) {
        stickyNav.classList.remove('is-sticky');
      }
    });
    window.dispatchEvent(new Event('scroll'));
  }
}

function initSimlecastPlayer() {
  $('.lp-btn-icon__play-simplecast').on('click', (e) => {
    e.preventDefault();
    const el = $(e.currentTarget);
    const href = el.attr('href');
    if (href) {
      $.get(`https://api.simplecast.com/oembed?url=${href}&type=mini`, (simplecastResponse) => {
        const playerWrap = el.parents('.bio-lp-latest-ep__container')
          .find('.bio-lp-player');
        playerWrap.html(simplecastResponse.html);
      });
    }
  });

  $('[data-episode-mini-play]').on('click', (e) => {
    e.preventDefault();
    const el = $(e.currentTarget).parent('a');
    const href = el.attr('href');
    const showsList = {
      '16minutes': '16 Minutes News',
      a16z: 'a16z Podcast',
      'bio-eats-world': 'Bio Eats World',
      'a16z-live': 'a16z Live',
    };

    if (href) {
      $.get(`https://api.simplecast.com/oembed?url=${href}`, (simplecastResponse) => {
        const header = $('#header');
        const dataImage = simplecastResponse.thumbnail_url;
        const dataTitle = simplecastResponse.title;
        const dataSubtitle = /(?:http[s]*:\/\/)*(.*?)\.(?=[^/]*\..{2,5})/
          .exec(href)[1].replaceAll('https%3A%2F%2F', '');
        let headerPlayerWrapper = $('#header-player-mini');
        if (! headerPlayerWrapper.length) {
          headerPlayerWrapper = $('<div/>')
            .attr('id', 'header-player-mini')
            .appendTo(header);
        }

        headerPlayerWrapper.empty();

        if (document.body.classList.contains('enable-announcement-bar')) {
          headerPlayerWrapper.css('top', $('html .info-bar').outerHeight());
        }

        const headerPlayerImageWrap = $('<div/>')
          .addClass('header-player-img')
          .appendTo(headerPlayerWrapper);

        $('<img/>').attr('src', dataImage)
          .appendTo(headerPlayerImageWrap);

        const headerPlayer = $('<div/>')
          .attr('id', 'header-player')
          .appendTo(headerPlayerWrapper);

        const headerTitleWrapper = $('<div/>')
          .addClass('header-player-title-wrap')
          .appendTo(headerPlayerWrapper);

        $('<div/>').addClass('header-player-subtitle')
          .text(showsList[dataSubtitle])
          .appendTo(headerTitleWrapper);

        $('<div/>').addClass('header-player-title')
          .text(dataTitle)
          .appendTo(headerTitleWrapper);

        const headerPlayerSimplecastWrap = $('<div/>')
          .addClass('header-player-simplecast-wrap')
          .appendTo(headerPlayerWrapper);

        $('<a/>').attr('href', href)
          .appendTo(headerPlayerSimplecastWrap);

        document.body.classList.add('header-player-mini-show');
        const player = simplecastResponse.html
          .replace(/src="([^"]+)"/, 'src="$1?color=F1FAFF"');
        headerPlayer.html(player);
      });
    }
  });
}

function initFormFocus() {
  setTimeout(() => {
    $('.mktoField').each(function () {
      if ('' !== $(this).val()) {
        $(this).closest('.mktoFieldWrap').addClass('input-active');
      }
    });
    $(document).on('focus', '.mktoField', function () {
      $(this).closest('.mktoFieldWrap').addClass('input-active');
    });
    $(document).on('blur', '.mktoField', function () {
      if ('' !== $(this).val()) {
        $(this).closest('.mktoFieldWrap').addClass('input-active');
      } else {
        $(this).closest('.mktoFieldWrap').removeClass('input-active');
      }
    });

    // eslint-disable-next-line max-len
    $(document).on('change', 'select[name="eventRegistrationUpdate"]', function () {
      let selectedVal = $(this).val();
      selectedVal = selectedVal.split(' ').join('-').toLowerCase();

      // eslint-disable-next-line max-len
      $(this).parents('.mktoFormRow').removeClass('cancel-my-registration update-my-registration');
      $(this).parents('.mktoFormRow').addClass(selectedVal);
    });
  }, 300);
}

